import React from "react";
import Product from "./Product";
import "../styles/Products.css";

import PatinHidraulico from "../img/patin-hidraulico.jpg";
import PatinHidraulicoConBascula from "../img/patin-hidraulico-con-bascula.jpg";
import PatinElectrico from "../img/patin-electrico.jpg";
import PatinHidraulicoDeTijera from "../img/patin-hidraulico-de-tijera.jpg";
import PatinParaRollos from "../img/patin-para-rollos.jpg";
import PatinTodoTerreno from "../img/patin-todo-terreno.jpg";
import MesaHidraulica from "../img/mesa-hidraulica-de-trabajo.jpg";
import ApiladorSemiElectrico from "../img/apilador-semi-electrico.png";
import ApiladorHidraulicoManual from "../img/apilador-hidraulico-manual.jpg";

function Products() {
    const prods = [
        {
            name: "Patin hidraulico",
            img: PatinHidraulico,
            description: "3000 Kg"
        },
        {
            name: "Patin con bascula",
            img: PatinHidraulicoConBascula,
            description: "2000 Kg"
        },
        {
            name: "Patin electrico",
            img: PatinElectrico,
            description: " "
        },
        {
            name: "Patin hidraulico de tijera",
            img: PatinHidraulicoDeTijera,
            description: "1000 Kg"
        },
        {
            name: "Patin para rollos",
            img: PatinParaRollos,
            description: "1000 Kg"
        },
        {
            name: "Patin todo terreno",
            img: PatinTodoTerreno,
            description: " "
        },
        {
            name: "Mesa hidraulica",
            img: MesaHidraulica,
            description: "1000 Kg"
        },
        {
            name: "Apilador semi electrico",
            img: ApiladorSemiElectrico,
            description: "1000 Kg"
        },
        {
            name: "Apilador hidraulico manual",
            img: ApiladorHidraulicoManual,
            description: "1000 Kg"
        }
    ];

    const products = [];
    for (let i = 0; i < prods.length; i++) {
        products.push(
            <Product
                name={prods[i].name}
                img={prods[i].img}
                description={prods[i].description}
            />
        );
    }

    return(
        <div className="products">
            {products}
        </div>
    );
}

export default Products;