import { React, useState } from "react";
import "../styles/Contact.css";
import { Alert } from "@mui/material";
import { ColorRing } from "react-loader-spinner";
import { IoMdCloseCircle } from "react-icons/io";

function Contact({ handleClose }) {

    const [showSuccessAlert, setShowSuccessAlert] = useState(false);
    const [showErrorAlert, setShowErrorAlert] = useState(false);
    const [showLoading, setShowLoading] = useState(false);

    const [formData, setFormData] = useState({
        nombre: '',
        correo: '',
        telefono: '',
        lugar: '',
        detalles: ''
    });

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    const handleSubmit = (e) => {
        e.preventDefault();

        setShowSuccessAlert(false);
        setShowErrorAlert(false);
        setShowLoading(true);

        fetch('http://44.211.136.200:5000/order', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(formData),
        })
        .then(data => {
            if (data.ok) {
                console.log("OK");
                setShowLoading(false);
                setShowSuccessAlert(true);
                //handleClose();
            } else {
                setShowLoading(false);
                setShowErrorAlert(true);
                console.log(data);
            }
        })
        .catch(error => {
            console.error('Error:', error);
            setShowLoading(false);
            setShowErrorAlert(true);
        });
    };

    return (
        <div className="contact">
            <div className="contact-form">
                <form onSubmit={handleSubmit} className="form">
                    <button onClick={handleClose} className="btn-close"><IoMdCloseCircle className="close-icon"/></button>
                    <div className="form-title">
                        <h3>Estamos listos para trabajar contigo</h3>
                        <p>Ingresa tus datos y te contactaremos</p>
                    </div>
                    <input name="nombre" type="text" onChange={handleInputChange} value={formData.nombre} placeholder="Empresa o nombre" className="form-input" required />
                    <input name="correo" type="mail" onChange={handleInputChange} value={formData.correo} placeholder="Correo" className="form-input" required />
                    <input name="telefono" type="number" onChange={handleInputChange} value={formData.telefono} placeholder="Teléfono" className="form-input" required />
                    <input name="lugar" type="text" onChange={handleInputChange} value={formData.lugar} placeholder="Lugar del servicio (municipio y estado)" className="form-input" required />
                    <textarea name="detalles" onChange={handleInputChange} value={formData.detalles} placeholder="Detalles de su solicitud" className="form-textarea" required />
                    {showErrorAlert && <Alert severity="error" className="alert">Ocurrio un error al registrar su solicitud, por favor intente de nuevo.</Alert>}
                    {showSuccessAlert && <Alert severity="success" className="alert">Solicitud registrada exitosamente.</Alert>}
                    {showLoading &&  <div className="loader">
                        <ColorRing
                        visible={true}
                        height="80"
                        width="80"
                        ariaLabel="color-ring-loading"
                        wrapperStyle={{}}
                        wrapperClass="color-ring-wrapper"
                        colors={['#ffffff', '#e9ebea', '#ffffff', '#e9ebea', '#ffffff']}
                        />
                    </div>}
                    <input type="submit" value="Enviar" className="btn-enviar" />
                </form>
            </div>
        </div>
    );
}

export default Contact;