import React, { useEffect, useState } from "react";
import logo from "../logo.png";
import "../styles/Header.css";
import Link from "./Link";

function Header() {
    const [home, setHome] = useState(false);
    const [products, setProducts] = useState(false);
    const [services, setServices] = useState(false);

    const [isScrolled, setIsScrolled] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            setIsScrolled(window.scrollY > 0); // Adjust the threshold as needed
        };

        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll); // Cleanup
    }, []);

    return (
        <div className={isScrolled ? "header shadow" : "header"}>
            <div className="logo">
                <img src={logo} className="app-logo" alt="logo" />
            </div>
            <div className="nav">
                <Link text="Inicio" to="/" active={home} onClick={() => console.log('click')} />
                <Link text="Productos" to="/productos" active={products} />
                <Link text="Servicios" to="/servicios" active={services} />
            </div>
        </div>
    );
}

export default Header;